import React from 'react'
import { Button, Form, Grid, Header, Image, Message, Segment, Checkbox } from 'semantic-ui-react'
import { Link } from 'react-router-dom'

class Login extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      now_page: "login",
      email: "",
      password: ""
    }
  }
  login = () => {
    fetch("https://cors-anywhere.herokuapp.com/https://freeticket.herokuapp.com/login?email=" + this.state.email + "&password=" + this.state.password)
      .then(response => response.json())
      .then(data => {
        if (data.type === "ok") {
          alert("Вы успешно авторизованы.")
          localStorage.setItem("token", data.token)
          localStorage.setItem("user_type", data.user_type)
          this.props.history.push('/')
        } else {
          alert("Неверные данные.")
        }
      })
  }
  reg = () => {
    fetch("https://cors-anywhere.herokuapp.com/https://freeticket.herokuapp.com/register?email=" + this.state.email + "&password=" + this.state.password + "&type=" + this.state.value + "&first_name=" + this.state.first_name + "&last_name=" + this.state.last_name)
      .then(response => response.json())
      .then(data => {
        if (data.items === "Success") {
          alert("Вы успешно зарегистрированы. Войдите на сайт.")
          this.setState({
            now_page: "login"
          })
        } else {
          alert("Ошибка. Попробуйте еще раз")
        }
      })
  }
  render() {
    return (
      <Grid textAlign='center' style={{ height: '100vh' }} verticalAlign='middle'>
        <Grid.Column style={{ maxWidth: 450 }}>
          <Header as='h2' color='teal' textAlign='center'>
            {this.state.now_page === "login" ? "Логин" : "Регистрация"}
          </Header>
          {
            this.state.now_page === "login" ?
              <div>
                <Form size='large'>
                  <Segment stacked>
                    <Form.Input fluid icon='user' iconPosition='left' placeholder='E-mail address' onChange={(e) => {
                      this.setState({
                        "email": e.target.value
                      })
                    }} />
                    <Form.Input
                      fluid
                      icon='lock'
                      iconPosition='left'
                      placeholder='Password'
                      type='password'
                      onChange={(e) => this.setState({
                        password: e.target.value
                      })}
                    />

                    <Button onClick={this.login} color='teal' fluid size='large'>
                      Войти
                          </Button>
                  </Segment>
                </Form>
                <Message>
                  Нет аккаунта? <a to='#' onClick={() => {
                    this.setState({
                      now_page: "reg"
                    })
                  }}>Регистрация</a>
                </Message>
              </div> :
              <div>
                <Form size='large'>
                  <Segment stacked>
                    <Form.Input fluid icon='user' iconPosition='left' placeholder='Имя' onChange={(e) => {
                      this.setState({
                        "first_name": e.target.value
                      })
                    }} />
                    <Form.Input fluid icon='user' iconPosition='left' placeholder='Фамилия' onChange={(e) => {
                      this.setState({
                        "last_name": e.target.value
                      })
                    }} />
                    <Form.Input fluid icon='user' iconPosition='left' placeholder='E-mail адрес' onChange={(e) => {
                      this.setState({
                        "email": e.target.value
                      })
                    }} />
                    <Form.Input
                      fluid
                      icon='lock'
                      iconPosition='left'
                      placeholder='Пароль'
                      type='password'
                      onChange={(e) => this.setState({
                        password: e.target.value
                      })}
                    />
                    <Form.Field>
                      <Checkbox
                        radio
                        label='Организатор'
                        name='checkboxRadioGroup'
                        value='org'
                        checked={this.state.value === 'org'}
                        onChange={(e) => this.setState({ value: "org" })}
                      />
                    </Form.Field>
                    <Form.Field>
                      <Checkbox
                        radio
                        label='Покупатель'
                        name='checkboxRadioGroup'
                        value='client'
                        checked={this.state.value === 'client'}
                        onChange={(e) => this.setState({ value: "client" })}
                      />
                    </Form.Field>
                    <Button onClick={this.reg} color='teal' fluid size='large'>
                      Регистрация
                        </Button>
                  </Segment>
                </Form>
                <Message>
                  Уже есть аккаунт? <a to='#' onClick={() => {
                    this.setState({
                      now_page: "login"
                    })
                  }}>Войти</a>
                </Message>
              </div>

          }
        </Grid.Column>
      </Grid>
    )
  }
}

export default Login