import React from 'react'
import { Card, Icon, Segment, Container, Header, Grid, Button, Divider } from 'semantic-ui-react'

import EventCardOrg from './EventCardOrg'

class OrgLk extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }
    }
    componentDidMount() {
        fetch("https://freeticket.herokuapp.com/my_events?token=" + localStorage.getItem("token"))
            .then(response => response.json())
            .then(data => {
                this.setState({
                    items: data.items
                })
            })
        fetch("https://freeticket.herokuapp.com/balance?token=" + localStorage.getItem("token"))
            .then(response => response.json())
            .then(data => {
                this.setState({
                    balance: data.balance
                })
            })
    }
    remove = (e) => {
        fetch("https://freeticket.herokuapp.com/remove_event?token=" + localStorage.getItem("token") + "&id=" + e.target.id)
            .then(response => response.json())
            .then(data => {
                alert("Успешно удалено")
                window.location.reload()
            })
    }
    render() {
        return (
            <div>
                <Segment>
                    <Container>
                        <Header as="h1">Здравствуйте!</Header>
                        <Header as="h3">Ваш баланс: {this.state.balance} рублей</Header>
                        <div style={{
                            display: "flex"
                        }}>
                            <Button color="olive">Вывести деньги</Button>
                            <Button as="Link" to="/add-event" primary>Добавить мероприятие</Button>
                        </div>
                        <Divider />
                        <Header as="h3">Ваши мероприятия</Header>
                        {
                            this.state.items && this.state.items.length < 1 && <Header>
                                Вы не добавили еще ни одного мероприятия
                            </Header>
                        }
                        <Grid celled={false}>
                            <Grid.Row>
                                {
                                    this.state.items && this.state.items.map((item, i) =>
                                        <Grid.Column width={5}>
                                            <EventCardOrg
                                                src={item.image}
                                                name={item.name}
                                                place={item.place}
                                                description={item.description}
                                                price={item.cost}
                                                role={localStorage.getItem("user_type")}
                                                key={item.event_id}
                                                id={item.event_id}
                                                onRemove={this.remove}
                                                onView={() => console.log("views")}
                                            />
                                        </Grid.Column>
                                    )
                                }
                            </Grid.Row>
                        </Grid>
                    </Container>
                </Segment>
            </div>
        )
    }
}

export default OrgLk