import React from 'react'
import { Card, Icon, Segment, Container, Header, Grid, Button, Divider } from 'semantic-ui-react'

import EventCard from './EventCard'

class ClientLk extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }
    }
    componentDidMount() {
        fetch("https://freeticket.herokuapp.com/user_events?token=" + localStorage.getItem("token"))
            .then(response => response.json())
            .then(data => {
                console.log(data)
                this.setState({
                    items: data.items
                })
            })
    }
    render() {
        return (
            <div>
                <Segment>
                    <Container>
                        <Header as="h1">Здравствуйте!</Header>
                        <Header as="h3">Ваши мероприятия</Header>
                        {
                            this.state.items && this.state.items.length < 1 && <Header>
                                Вы не купили ни одного билета
                            </Header>
                        }
                        <Grid celled={false}>
                            <Grid.Row>
                                {
                                    this.state.items && this.state.items.map((item, i) =>
                                        <Grid.Column width={5}>
                                            <EventCard
                                                src={item.image}
                                                name={item.name}
                                                place={item.place}
                                                description={item.description}
                                                price={item.cost}
                                                key={item.event_id}
                                                id={item.event_id}
                                            />
                                        </Grid.Column>
                                    )
                                }
                            </Grid.Row>
                        </Grid>
                    </Container>
                </Segment>
            </div>
        )
    }
}

export default ClientLk