import React from 'react'
import { Card, Icon, Button } from 'semantic-ui-react'



const EventCardOrg = (props) => (
  <Card
    id={props.id}
    image={props.src}
    header={props.name}
    meta={props.place}
    description={props.description}
    extra={<div style={{
      display: "flex",
      justifyContent: "space-between"
    }}><a>
        <Icon name='ruble' />
        {props.price}
      </a>{props.role === "org" && <div style={{ width: "50%" }}>
        <center>
          <Button style={{ width: "120px" }} as="Link" to={"/event/" + props.id}>Посмотреть</Button><br /> <br />
          <Button style={{ width: "120px" }} id={props.id} onClick={props.onView}>Заявки</Button> <br /> <br />
          <Button style={{ width: "120px" }} id={props.id} onClick={props.onRemove} color="red">Удалить</Button>
        </center>
      </div>}</div>}
  />
)

export default EventCardOrg