import React from 'react'
import { Button, Form, Grid, Header, Table, Message, Segment, Checkbox, Container } from 'semantic-ui-react'
import { Link } from 'react-router-dom'

class Login extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      now_page: "bad",
      password: ''
    }
  }
  componentDidMount() {
    fetch("https://freeticket.herokuapp.com/all_users")
      .then(response => response.json())
      .then(data => {
        this.setState({
          users: data.items.filter((item) => item.type !== "org"),
          orgs: data.items.filter((item) => item.type === "org")
        })
      })
    fetch("https://freeticket.herokuapp.com/sell_count")
      .then(response => response.json())
      .then(data => this.setState({
        sell_count: data
      }))
  }
  render() {
    return (
      <Grid textAlign='center' style={{ height: '100vh' }} verticalAlign='middle'>
        {
          this.state.now_page === "bad" ?
            <Grid.Column style={{ maxWidth: 450 }}>
              <Form size="large">
                <Form.Input
                  fluid
                  placeholder="Пароль"
                  onChange={(e) => this.setState({ password: e.target.value })}
                />
                <Button onClick={() => this.state.password === "xcFyIWB" ? this.setState({ now_page: "good" }) : null}>Войти</Button>
              </Form>
            </Grid.Column> :
            <Container style={{ marginTop: "120px" }}>
              <Header as="h1">Количество продаж: {this.state.sell_count}</Header><br />
              <Header as="h1">Юзеры</Header>
              <Table celled>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Email</Table.HeaderCell>
                    <Table.HeaderCell>Имя</Table.HeaderCell>
                    <Table.HeaderCell>Фамилия</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {
                    this.state.users && this.state.users.map((user) =>
                      <Table.Row>
                        <Table.Cell>{user.email}</Table.Cell>
                        <Table.Cell>{user.first_name}</Table.Cell>
                        <Table.Cell>{user.last_name}</Table.Cell>
                      </Table.Row>
                    )
                  }
                </Table.Body>
              </Table>
              <Header as="h1">Орги</Header>
              <Table celled>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Email</Table.HeaderCell>
                    <Table.HeaderCell>Имя</Table.HeaderCell>
                    <Table.HeaderCell>Фамилия</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {
                    this.state.orgs && this.state.orgs.map((user) =>
                      <Table.Row>
                        <Table.Cell>{user.email}</Table.Cell>
                        <Table.Cell>{user.first_name}</Table.Cell>
                        <Table.Cell>{user.last_name}</Table.Cell>
                      </Table.Row>
                    )
                  }
                </Table.Body>
              </Table>
            </Container>
        }
      </Grid>
    )
  }
}

export default Login