import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import {
  Button,
  Container,
  Divider,
  Grid,
  Header,
  Icon,
  Image,
  Form,
  Menu,
  Responsive,
  Segment,
  Sidebar,
  Visibility,
} from 'semantic-ui-react'

import OrgLk from '../Components/OrgLk'
import ClientLk from '../Components/ClientLk'
import Footer from '../Components/Footer'

// Heads up!
// We using React Static to prerender our docs with server side rendering, this is a quite simple solution.
// For more advanced usage please check Responsive docs under the "Usage" section.
const getWidth = () => {
  const isSSR = typeof window === 'undefined'

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}


/* Heads up!
 * Neither Semantic UI nor Semantic UI React offer a responsive navbar, however, it can be implemented easily.
 * It can be more complicated, but you can create really flexible markup.
 */
class DesktopContainer extends Component {
  state = {}

  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 80, padding: '1em 0em' }}
            vertical
          >
            <Menu
              fixed={fixed ? 'top' : null}
              inverted={!fixed}
              pointing={!fixed}
              secondary={!fixed}
              size='large'
            >
              <Container>
                <Menu.Item as={Link} to="/">
                  Главная
                  </Menu.Item>
                <Menu.Item as={Link} to="/events">Все мероприятия</Menu.Item>
                {
                  localStorage.getItem("token") ? <Menu.Item position="right"><Button as={Link} to="/lk" inverted={!fixed}>
                    Личный кабинет
                  </Button></Menu.Item> :
                    <Menu.Item position='right'>
                      <Button as={Link} to="/login" inverted={!fixed}>
                        Войти
                        </Button>
                      <Button as={Link} to="/login" inverted={!fixed} primary={fixed} style={{ marginLeft: '0.5em' }}>
                        Регистрация
                        </Button>
                    </Menu.Item>

                }
              </Container>
            </Menu>
          </Segment>
        </Visibility>

        {children}
      </Responsive>
    )
  }
}

DesktopContainer.propTypes = {
  children: PropTypes.node,
}

class MobileContainer extends Component {
  state = {}

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state

    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >
          <Menu.Item as={Link} to="/">
            Главная
            </Menu.Item>
          <Menu.Item as={Link} to="/events">Все мероприятия</Menu.Item>
          {
            localStorage.getItem("token") ? <Menu.Item to="/lk">Личный кабинет</Menu.Item> : <div>
              <Menu.Item to="/login" as={Link}>Войти</Menu.Item>
              <Menu.Item to="login" as={Link}>Регистрация</Menu.Item>
            </div>
          }
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 350, padding: '1em 0em' }}
            vertical
          >
            <Container>
              <Menu inverted pointing secondary size='large'>
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name='sidebar' />
                </Menu.Item>
                <Menu.Item position='right'>
                  <Button to="/login" as={Link} inverted>
                    Войти
                    </Button>
                  <Button to="login" as={Link} inverted style={{ marginLeft: '0.5em' }}>
                    Регистрация
                    </Button>
                </Menu.Item>
              </Menu>
            </Container>
          </Segment>

          {children}
        </Sidebar.Pusher>
      </Responsive>
    )
  }
}

MobileContainer.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}

class EventPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    fetch("https://freeticket.herokuapp.com/bought?token=" + localStorage.getItem("token") + "&id=" + this.props.match.params.id)
      .then(response => response.json())
      .then(result => {
        fetch("https://freeticket.herokuapp.com/get_event?id=" + this.props.match.params.id)
          .then(response => response.json())
          .then(data => {
            console.log(result.type)
            this.setState({
              bought: result.type,
              item: data.items
            })
          })
      })
  }
  buy = () => {
    if (localStorage.getItem("token")) {
      fetch("https://freeticket.herokuapp.com/buy_event?token=" + localStorage.getItem("token") + "&id=" + this.props.match.params.id)
        .then(response => response.json())
        .then(data => {
          console.log(data)
        })
    } else {
      this.props.history.push('/login')
    }
  }
  render() {
    return (
      <ResponsiveContainer>
        <Container style={{
          marginTop: "60px",
          marginBottom: "60px"
        }}>
          <Header as="h2">Мероприятие</Header>
          {
            this.state.item && <div>
              <div style={{
                display: "flex",
                justifyContent: "space-between"
              }}>
                <Header as="h1">{this.state.item.name}</Header>
                <br />
                {
                  localStorage.getItem("user_type") === "client" && !this.state.bought && <Button onClick={this.buy} primary>Купить билет за {this.state.item.cost} рублей</Button>
                }
              </div>
              <p><b>Место проведения:</b> {this.state.item.place}</p>
              <p><b>Дата:</b> {this.state.item.date}</p>
              <center>
                <Image src={this.state.item.image} />
              </center>
              <p>{this.state.item.description}</p>
            </div>
          }
        </Container>
        <Footer />
      </ResponsiveContainer>
    )
  }
}
export default EventPage