import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import {
  Button,
  Container,
  Divider,
  Grid,
  Header,
  Icon,
  Image,
  List,
  Menu,
  Responsive,
  Segment,
  Sidebar,
  Visibility,
} from 'semantic-ui-react'

import EventCard from '../Components/EventCard'
import Footer from '../Components/Footer'

// Heads up!
// We using React Static to prerender our docs with server side rendering, this is a quite simple solution.
// For more advanced usage please check Responsive docs under the "Usage" section.
const getWidth = () => {
  const isSSR = typeof window === 'undefined'

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}


/* Heads up!
 * Neither Semantic UI nor Semantic UI React offer a responsive navbar, however, it can be implemented easily.
 * It can be more complicated, but you can create really flexible markup.
 */
class DesktopContainer extends Component {
  state = {}

  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 80, padding: '1em 0em' }}
            vertical
          >
            <Menu
              fixed={fixed ? 'top' : null}
              inverted={!fixed}
              pointing={!fixed}
              secondary={!fixed}
              size='large'
            >
              <Container>
                <Menu.Item as={Link} to="/">
                  Главная
                  </Menu.Item>
                <Menu.Item active as={Link} to="/events">Все мероприятия</Menu.Item>
                {
                  localStorage.getItem("token") ? <Menu.Item position="right"><Button as={Link} to="/lk" inverted={!fixed}>
                    Личный кабинет
                  </Button></Menu.Item> :
                    <Menu.Item position='right'>
                      <Button as={Link} to="/login" inverted={!fixed}>
                        Войти
                        </Button>
                      <Button as={Link} to="/login" inverted={!fixed} primary={fixed} style={{ marginLeft: '0.5em' }}>
                        Регистрация
                        </Button>
                    </Menu.Item>

                }
              </Container>
            </Menu>
          </Segment>
        </Visibility>

        {children}
      </Responsive>
    )
  }
}

DesktopContainer.propTypes = {
  children: PropTypes.node,
}

class MobileContainer extends Component {
  state = {}

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state

    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >
          <Menu.Item as={Link} to="/">
            Главная
            </Menu.Item>
          <Menu.Item active as={Link} to="/events">Все мероприятия</Menu.Item>
          {
            localStorage.getItem("token") ? <Menu.Item to="/lk">Личный кабинет</Menu.Item> : <div>
              <Menu.Item to="/login" as={Link}>Войти</Menu.Item>
              <Menu.Item to="login" as={Link}>Регистрация</Menu.Item>
            </div>
          }
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 350, padding: '1em 0em' }}
            vertical
          >
            <Container>
              <Menu inverted pointing secondary size='large'>
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name='sidebar' />
                </Menu.Item>
                <Menu.Item position='right'>
                  <Button to="/login" as={Link} inverted>
                    Войти
                    </Button>
                  <Button to="login" as={Link} inverted style={{ marginLeft: '0.5em' }}>
                    Регистрация
                    </Button>
                </Menu.Item>
              </Menu>
            </Container>
          </Segment>

          {children}
        </Sidebar.Pusher>
      </Responsive>
    )
  }
}

MobileContainer.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}

class AllEvents extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    fetch("https://freeticket.herokuapp.com/all_events")
      .then(response => response.json())
      .then(data => {
        this.setState({
          items: data.items
        })
      })
  }
  render() {
    return (
      <ResponsiveContainer>
        <Segment style={{ border: '0px' }}>
          <Container>
            <Header as="h1">
              Все мероприятия
                      </Header>
            <Grid columns={2} celled={false} divided={false} stackable>
              <Grid.Row>
                {
                  this.state.items && this.state.items.map((item, i) =>
                    <Grid.Column style={{ marginTop: "30px" }} width={5} divided={false}>
                      <EventCard
                        id={item.event_id}
                        src={item.image}
                        name={item.name}
                        place={item.place}
                        description={item.description}
                        price={item.cost}
                      />
                    </Grid.Column>
                  )
                }
              </Grid.Row>
            </Grid>
          </Container>
        </Segment>
        <Footer />
      </ResponsiveContainer>
    )
  }
}
export default AllEvents